.mp-block-disclaimer {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: -30px;
    display: flex;
    align-items: center;
    justify-content: center;
    display: none;
    gap: 5px;
    img {
        height: 16px;
        width: auto;
    }

    p {
        font-size: 14px;
        font-weight: 600;
    }
}
.mp-block-outside-header {
    position: absolute;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    top: 0;
    left: 0;
    transform: translateY(-100%);
}
.mp-block-outside-header-title {
    font-size: 13px;
    font-weight: 600;
} 
.mp-block-outside-header-btn-block {
    display: flex;
    align-items: center;
    gap: 10px;
}
.mp-block-outside-header-btn {
    font-size: 13px;
    font-weight: 600;
    opacity: 0.5;
    cursor: pointer;
    
    &:hover {
        opacity: 0.75;
    }
}
.mp-block-outside-header-btn-active {
    opacity: 1;

}
.mp-block-outside-header-date {
    font-size: 13px;
    font-weight: 600;

    outline: none;
    border: none;
    background-color: #fff;
    max-width: 170px;
}
.mp-block-outside-header-select {
    outline: none;
    border: none;
    background-color: unset;

    font-size: 13px;
    font-weight: 600;
    text-align: right;
}


/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* HERD MAIN PAGE */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
.herd-mp-flex-block {
    width: 100%;
    display: grid;
    align-items: center;
    gap: 30px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    position: relative;
}
.herd-mp-quick-info-block {
    width: 100%;
    aspect-ratio: 1/1.25;
    background-color: #f0f0f0;
    border-radius: 20px;
    grid-column: 1/4;
    position: relative;
    overflow: hidden;
}
.herd-mp-quick-info-counter-block {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    align-items: center;
    gap: 10px;
    z-index: 150;
}
.herd-mp-quick-info-counter {
    width: 10px;
    height: 10px;
    background-color: #D9D9D9;
    border-radius: 50%;
    cursor: pointer;

    &:hover {
        background-color: $herd;
    }
}
.herd-mp-quick-info-counter-active {
    background-color: $herd;
}
.herd-mp-quick-info-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}
.herd-mp-quick-info-item-img {
    width: 50%;
    height: auto;
    position: relative;
    display: flex;
    align-items: center;
}

.herd-mp-quick-info-item-img-shadow {
    width: 45%;
    height: 15px;
    border-radius: 50%;
    background-color: #D9D9D9;
    filter: blur(5px);
}
.herd-mp-quick-info-item-title {
    font-size: 30px;
    font-weight: 500;
    margin-top: 40px;
    width: 80%;
    text-align: center;
}
.herd-mp-quick-info-item-res {
    font-size: 60px;
    font-weight: 600;
    margin-top: 5px;
}

.herd-mp-list-block-combined {
    height: calc(450px - 0px);
    width: calc(100% - 0px);
    grid-column: 4/10;
    position: relative;

}
.herd-mp-list-block {
    background-color: #f0f0f0;
    border-radius: 20px;
    height: calc(100% - 40px);
    width: calc(100% - 40px);
    overflow: auto;
    position: relative;
    padding: 20px;
}
.herd-mp-list-block::-webkit-scrollbar {
    display: none;
}

.herd-mp-list-block-header {
    width: 100%;
    height: calc(40px - 1.5px);
    display: grid;
    position: sticky;
    left: 0;
    top: 0;
    margin-bottom: 20px;
}
.herd-mp-list-block-insem .herd-mp-list-block-header,
.herd-mp-list-block-calv .herd-mp-list-block-header{
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.herd-mp-list-block-calv {
    display: none;
}
.herd-mp-li-header-item {
    width: calc(100% - 21.5px);
    padding: 0 10px;
    height: 100%;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 600;
    color: #818181;
    overflow: hidden;
}
.herd-mp-li-header-item:last-of-type {
    border-right: unset;
}
.herd-mp-list-container {
    width: 100%;
    height: calc(100% - 40px);
    overflow: auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.herd-mp-list-container::-webkit-scrollbar {
    display: none;
}
.herd-mp-list-line {
    width: 100%;
    display: grid;
    position: relative;
    text-decoration: none;
    gap: 10px;
    border-radius: 10px; 
    flex-shrink: 0;

    &:hover {
        text-decoration: underline;
    }
}
.herd-mp-list-line:last-of-type {
    border-bottom: unset;
}
.herd-mp-list-block-insem .herd-mp-list-line,
.herd-mp-list-block-calv .herd-mp-list-line {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.herd-mp-list-line-item {
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 600;
    overflow: hidden;
    background-color: #d9d9d9;
    border-radius: 10px;
}
.herd-mp-list-line-item:last-of-type {
    border-right: unset;
}
.herd-mp-list-line-indicator {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
}
.herd-mp-list-line-red .herd-mp-list-line-indicator {
    background-color: rgb(212, 77, 92, 0.5);
}
.herd-mp-list-line-yellow .herd-mp-list-line-indicator {
    background-color: rgb(246, 185, 29, 0.5);
}
.herd-mp-list-line-red .herd-mp-list-line-indicator-text {
    /* color: #d44d5c; */
}
.herd-mp-list-line-yellow .herd-mp-list-line-indicator-text {
    /* color: #f6b91d; */
}


.mp-herd-top-results-block {
    width: 100%;
    height: 450px;
    display: flex;
    align-items: flex-start;
    gap: 30px;
    position: relative;
    z-index: 100;
}

.mp-herd-tr-podium {
    width: 45%;
    height: 100%;
    position: relative;
}
.mp-herd-tr-podium-step-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    aspect-ratio: 1/1;
    position: absolute;

    img {
        width: 30%;
        height: auto;
        position: absolute;
        z-index: 99;
    }
}
.mp-herd-tr-podium-step-res {
    font-size: 36px;
    font-weight: 600;
    z-index: 100;
}
.mp-herd-tr-podium-step-number {
    font-size: 16px;
    font-weight: 600;
    margin-top: 10px;
    z-index: 100;
}
.mp-herd-tr-podium-step-name {
    font-size: 16px;
    font-weight: 600;
    opacity: 0.5;
    z-index: 100;
}
.mp-herd-tr-podium-step-1 {
    width: 66%;
    background-color: #FFD700;
    top: 0;
    left: 0;

    img {
        left: 0;
        bottom: 0;
    }
}
.mp-herd-tr-podium-step-2 {
    width: 50%;
    bottom: 0;
    right: 0;
    background-color: #C0C0C0;

    img {
        right: 0;
        top: 0;
    } 
    .mp-herd-tr-podium-step-res {
        font-size: 30px;
    }
    .mp-herd-tr-podium-step-number {
        font-size: 14px;
        margin-top: 5px;
    }
    .mp-herd-tr-podium-step-name {
        font-size: 14px;
    }
}
.mp-herd-tr-podium-step-3 {
    background-color: #cd7f32 ;
    width: 30%;
    bottom: 5px;
    left: 60px;

    img {
        right: 0;
        bottom: 0;
    } 
    .mp-herd-tr-podium-step-res {
        font-size: 24px;
    }
    .mp-herd-tr-podium-step-number {
        font-size: 12px;
        margin-top: 0px;
    }
    .mp-herd-tr-podium-step-name {
        font-size: 12px;
    }
}


.mp-herd-tr-list {
    width: 65%;
    max-height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 30px;
    overflow: auto;
}
.mp-herd-tr-list::-webkit-scrollbar {
    display: none;
}
.mp-herd-tr-list-item {
    width: 100%;
    aspect-ratio: 1/1;
    background-color: #D9D9D9;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}
.mp-herd-tr-list-item-place {
    position: absolute;
    left: -10px;
    transform: translate(-100%);
    font-size: 14px;
    font-weight: 600;
}
.mp-herd-tr-list-item-place-3 {
    color: #cd7f32 ;
}
.mp-herd-tr-list-item-place-1 {
    color: #FFD700;
}
.mp-herd-tr-list-item-place-2 {
    color: #C0C0C0;
}
.mp-herd-tr-list-item-number {
    font-size: 14px;
    font-weight: 600;
}
.mp-herd-tr-list-item-block-right {
    display: flex;
    align-items: center;
    gap: 10px;
}
.mp-herd-tr-list-item-res {
    font-size: 14px;
    font-weight: 600;
}
.mp-herd-tr-list-item-per-top {
    font-size: 12px;
    font-weight: 600;
    color: $success;
}
.mp-herd-tr-list-item-per-bottom {
    font-size: 12px;
    font-weight: 600;
    color: $error;
}





/* MAIN PAGE GRAPH */
.mp-herd-graph-block {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 30px;
    position: relative;
}
.mp-herd-graph-container {
    width: 100%;
    height: 400px;
    grid-column: 1/8;
    background-color: #f0f0f0;
    border-radius: 20px;
    position: relative;
}
.mp-herd-graph-load {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    backdrop-filter: blur(2px);
    z-index: 200;
}

.basic-graph-grid-line {
    stroke: #bdbdbd;
    stroke-width: 1;
    stroke-dasharray: 5,5;
}
.basic-graph-show-line {
    stroke: #0a0a0a;
    stroke-width: 1;
    stroke-dasharray: 5,5;
}
.basic-graph-tick {
    font-size: 12px;
    font-weight: 600;
}
.basic-graph-tick-date {
    font-size: 12px;
    font-weight: 600;
    transform: translate(-4.5%);
}
.mp-herd-legend-btn {
    position: absolute;
    bottom: 10px;
    right: 10px;
    width: calc(40px - 4px);
    height: calc(40px - 4px);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    cursor: pointer;
    z-index: 101;
    border: 2px solid #000000;

    ion-icon {
        font-size: 125%;
    }
}
.mp-herd-legend {
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: rgb(255, 255, 255, 0.75);
    width: calc(100% - 100px);
    height: calc(100% - 125px);
    padding: 75px 50px 50px 50px;
    border-radius: 10px;
    z-index: 100;
    display: none;
    gap: 50px;
}
.mp-herd-legend-column {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.mp-herd-legend-column-title {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 30px;
}
.mp-herd-legend-item {
    display: flex;
    align-items: center;
    gap: 15px;
    cursor: pointer;
}
.mp-herd-li-check {
    width: calc(20px - 4px);
    aspect-ratio: 1/1;
    border-radius: 3px;
    border: 2px solid #000;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;

    ion-icon {
        font-size: 75%;
        display: none;
    }
}
.mp-herd-li-check-checked {
    ion-icon {
        display: block;
    }
}
.mp-herd-li-mark {
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.mp-herd-li-mark-average {
    width: 100%;
    border-bottom: 2px solid #0a0a0a;
}
.mp-herd-li-mark-average-dot {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $herd;
    opacity: 0.5;
}
.mp-herd-li-mark-top-line {
    width: 100%;
    border-bottom: 2px dashed $success;
    opacity: 0.75;
}
.mp-herd-li-mark-bottom-line {
    width: 100%;
    border-bottom: 2px dashed $error;
    opacity: 0.75;
}
.mp-herd-li-mark-male-line {
    width: 100%;
    border-bottom: 2px solid $male;
}
.mp-herd-li-mark-female-line {
    width: 100%;
    border-bottom: 2px solid $female;
}

.mp-herd-li-text {
    font-size: 13px;
    font-weight: 600;
}
.mp-herd-legend-item-off {
    filter: grayscale(1);

    .mp-herd-li-text {
        text-decoration: line-through;
    }
}
.mp-hg-btn-block {
    position: absolute;
    top: 10px;
    display: flex;
    gap: 10px;
    align-items: center;
}
.mp-hg-btn-block-1 {
    left: 10px;
}
.mp-hg-btn-block-2 {
    right: 10px;
}
.mp-hg-btn {
    padding: 5px 10px;
    background-color: #fff;
    border: 2px solid #000000;
    border-radius: 50px;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 101;
    cursor: pointer;
    
    ion-icon {
        font-size: 18px;
    }
}
.mp-hg-btn-active {
    background-color: #000000;
    color: #fff;
    
    ion-icon {
        color: #fff;
    }
}
.mp-hg-btn-unav {
    pointer-events: none;
    opacity: 0.5;
}
.mp-graph-tooltip {
    width: calc(200px - 20px);
    padding: 10px 10px 45px 10px !important;
    border: 5px solid $herd;
    border-radius: 10px;
    background-color: #fff;
    position: absolute;
    z-index: 102;
    display: flex;
    flex-direction: column;

    line-height: unset !important;
    pointer-events: unset !important;
}
.mp-graph-tooltip-res {
    font-size: 48px;
    font-weight: 600;
    display: block;
}
.mp-graph-tooltip-sub-res {
    font-size: 32px;
    font-weight: 600;
}
.mp-graph-tooltip-title {
    font-size: 15px;
    font-weight: 600;
    opacity: 0.5;
}
.mp-graph-tooltip-gap {
    margin: 10px 0;
    width: 100%;
    height: 2px;
    background-color: #0a0a0a;
}
.mp-graph-tooltip-date {
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0;
    width: 100%;
    font-size: 12px;
    font-weight: 600;
}
.mp-graph-tooltip-date::before {
    content: '';
    width: 20%;
    height: 1.5px;
    background-color: #0a0a0a;
    border-radius: 10px;
    position: absolute;
    top: 0;
}
.basic-graph-point {
    stroke-width: 3px;
    stroke: $herd;
    fill: #f0f0f0;
    z-index: 77;
    cursor: pointer;
    &:hover {
        stroke-width: 8px;
    }
}
.basic-graph-text tspan {
    font-size: 48px !important;
    font-weight: 900 !important;
    fill: $herd !important;
}

.basic-graph-text-date tspan{
    font-size: 16px !important;
    font-weight: 600 !important;
    fill: $mainBlack !important;
}
.projected-graph-data {
    opacity: 0.5;
}
.basic-graph-average-dot {
    fill: #a2a2a280;
    z-index: 75;
    cursor: pointer;
    .basic-graph-average-dot-active {
        fill: #0a0a0a; 
    }

    &:hover {
        fill: #0a0a0a;
    }
}
.basic-graph-average-dot-top {
    .basic-graph-average-dot-active {
        fill: $success;
    }

    &:hover {
        fill: $success;
    }
}
.basic-graph-average-dot-bottom {
    .basic-graph-average-dot-active {
        fill: $error;
    }

    &:hover {
        fill: $error;
    }
}
.basic-graph-point-line {
    fill: transparent;
    stroke-width: 2px;
    stroke: #0a0a0a;
    stroke-linejoin: round;
    z-index: 76;
    stroke-dasharray: 2000;
    stroke-dashoffset: 2000;
    /* animation: fadeIn 1s ease-in; */
}
.basic-graph-point-line-male {
    fill: transparent;
    stroke-width: 2px;
    stroke: $male;
    stroke-linejoin: round;
    z-index: 76;
    stroke-dasharray: 2000;
    stroke-dashoffset: 2000;
    /* animation: fadeIn 1s ease-in; */
}
.basic-graph-point-line-female {
    fill: transparent;
    stroke-width: 2px;
    stroke: $female;
    stroke-linejoin: round;
    z-index: 76;
    stroke-dasharray: 2000;
    stroke-dashoffset: 2000;
    /* animation: fadeIn 1s ease-in; */
}
.basic-graph-point-line-trans {
    @extend .basic-graph-point-line;
    opacity: 0.25;
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.basic-graph-top-line {
    fill: transparent;
    stroke-width: 2px;
    stroke: $success;
    opacity: 0.5;
    stroke-linejoin: round;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
}
.basic-graph-bottom-line {
    fill: transparent;
    stroke-width: 2px;
    stroke: $error;
    opacity: 0.5;
    stroke-linejoin: round;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
}
.mp-herd-graph-info-block {
    width: calc(100% - 30px);
    height: calc(400px - 30px);
    padding: 15px;
    grid-column: 8/11;
    border-radius: 20px;
    background-color: #f0f0f0;
    position: relative;
    
    /* background-image: url('../img/backgrounds/herd-main-1.png'); */
    background-size: cover;
}
.mp-herd-graph-info-block-inner {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &:hover {
        overflow: auto;
    }
}

.mp-herd-graph-info-item {
    width: calc(100% - 24px);
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 2px solid #00000000;
    flex-shrink: 0;
}
.mp-herd-graph-info-item-group {
    display: flex;
    align-items: center;
    gap: 5px;
}
.mp-herd-graph-info-text {
    font-size: 14px;
    font-weight: 600;
}
.mp-herd-graph-info-sub-text {
    font-size: 14px;
    font-weight: 600;
    opacity: 0.5;
}
.mp-herd-graph-info-item-label {
    font-size: 10px;
    font-weight: 600;
    padding: 2px 5px;
    border-radius: 50px;
    display: none;
    color: #fff;
}
.mp-herd-graph-info-item-top {
    .mp-herd-graph-info-text,.mp-herd-graph-info-sub-text {
        color: $success;
    }

    .mp-herd-graph-info-item-label {
        background-color: $success;
        display: block;
    }
}
.mp-herd-graph-info-item-bottom {
    .mp-herd-graph-info-text,.mp-herd-graph-info-sub-text {
        color: $error;
    }
    .mp-herd-graph-info-item-label {
        background-color: $error;
        display: block;
    }
}

/* PROJECTION GRAPH */
.mp-projection-block {
    width: calc(100% - 0px);
    border-radius: 20px;
    height: calc(500px - 0px);
    display: flex;
    align-items: center;
    gap: 30px;
   position: relative;
   margin-top: 30px;
}
.mp-projection-graph {
    width: calc(50% - 40px);
    height: calc(100% - 120px);
    padding: 50px 20px 70px 20px;
    position: relative;
    border-radius: 20px;
    background-color: #f0f0f0;
}
.mp-projection-graph-working-area {
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    
}
.mp-projection-graph-item {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    gap: 10px;
    position: relative;
}
.mp-projection-graph-item:hover .mp-projection-graph-item-title{
    color: $herd;
}
.mp-projection-graph-item-title {
    font-size: 150%;
    font-weight: 600;
    position: absolute;
    bottom: -20px;
    transform: translateY(100%);
}
