/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* FORMS */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
.form-section {
  padding: 25px 0;
  width: 100%;
  min-height: calc(100vh - 50px);
  /* background: linear-gradient(#ffffffdc, #ffffffdc), url('../img/images/cows-login-bw.jpg'); */
  backdrop-filter: grayscale(100%);
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-attachment: fixed;
}

.common-form {
  width: 350px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.form-icon {
  width: 75px;
  height: 75px;
  border-radius: 5px;
  margin-bottom: 50px;
}

.form-title {
  color: #ffffff;
  font-size: 180%;
  font-weight: 400;
  margin-bottom: 7.5px;
  letter-spacing: -0.5px;
}

.form-sub-title {
  font-weight: 300;
  font-size: 125%;
}

.input-box {
  position: relative;
  margin-top: 15px;
  padding-top: 15px;
  width: 100%;
}

.common-form-input {
  width: calc(100% - 23px);
  background-color: #ffffff00;
  outline: none;
  border: none;
  border-bottom: 1.5px solid #3E363F;
  padding: 15px 10px;
  font-weight: 300;
}

.input-warning-text {
  width: calc(100% - 20px);
  padding: 0 10px;
  font-size: 85%;
  font-weight: 300;
  color: $error;
  position: absolute;
  bottom: -5px;
  transform: translateY(100%);
}

.common-form-label {
  color: #ffffff;
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  pointer-events: none;
}

.pass-visivility-btn {
  position: absolute;
  bottom: 15px;
  right: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pass-visivility-btn ion-icon {
  font-size: 20px;
}

.form-forget-pass {
  text-decoration: none;
  font-weight: 300;
  color: #f6b91d
}

.common-form-btn {
  width: 100%;
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  border-radius: 5px;
  background-color: #f6b91d;
  color: #000000;
  margin-top: 50px;
  margin-bottom: 30px;
  cursor: pointer;
  font-weight: 300;
  position: relative;
}

.common-form-btn:hover {
  background-color: #f6b91d;
}

.common-form-or {
  width: 100%;
  text-align: center;
  margin: 10px 0;
  font-weight: 300;
}

.alternative-login {
  width: calc(100% - 3px);
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.5px solid #000000;
  font-weight: 300;
  border-radius: 5px;
  color: #000000;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  backdrop-filter: blur(3px);
  margin-bottom: 30px;
}

.alternative-login ion-icon {
  position: absolute;
  left: 10px;
  font-size: 150%;
}

.form-final-text {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 300;
}

.form-final-text a {
  color: #f6b91d;
  text-decoration: none;
  margin-left: 5px;
}

.form-final-text a:hover {
  text-decoration: underline;
}

/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* DECIDE BLOCK */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
.ai-decide-block {
  width: 950px;
  height: 700px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  gap: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ai-decide-block-item {
  width: calc(100% - 60px);
  height: calc(100% - 60px);
  padding: 30px;
  background-color: #f0f0f0;
  border-radius: 10px;
  position: relative;
  cursor: pointer;
  border: 2px solid #ffffff00;
}
#sale {
  grid-column: 1/5;
  grid-row: 1/3;
}
#consumption {
  grid-column: 1/5;
  grid-row: 3/5;
}

#birth {
  grid-column: 1/5;
  grid-row: 1/4;
}
#purchase {
  grid-column: 3/5;
  grid-row: 1/4;
}
#dead {
  grid-column: 1/5;
  grid-row: 4/5;

  .ai-decide-block-item-text {
    top: 50%;
    transform: translateY(-50%);
  }

  .ai-decide-block-item-img {
    left: unset;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);

    height: 85%;
    width: auto;
  }
}

.ai-decide-block-item:hover {
  border-color: $mainBlack;
}

.ai-decide-block-item-text {
  position: absolute;
  font-size: 40px;
  font-weight: 600;
  top: 30px;
  left: 30px;
  width: 80%;
}

.ai-decide-block-item-text span {
  color: $herd;
  font-size: 100%;
  font-weight: 600;
}
.dist-module .ai-decide-block-item-text span {
  color: $dist;
}

.ai-decide-block-item-img {
  width: 220px;
  height: 220px;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%);
}


/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* TITLES AND BLOCKS */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
.add-animal-container {
  width: 1140px;
  margin: 0 auto;
  min-height: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  position: relative;
}

.animal-results-window {
  width: 100%;
  /* display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  column-gap: 10px; */
  min-height: calc(100vh - 100px);
  overflow: hidden;
  position: relative;
}
.animal-results-window-full {
  min-height: calc(100vh - 100px);
  padding: 50px 0;
}
.ar-history-block {
  position: absolute;
  top: 50px;
  transform: translateY(-50%);
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.ar-h-title {
  font-size: 14px;
  font-weight: 600;
}
.ar-h-container {
  display: flex;
  align-items: center;
  gap: 20px;
  overflow: hidden;
  padding: 20px 0;

  &:hover {
    overflow: auto;
  }
}
.ar-h-item {
  padding: 10px 15px;
  border-radius: 50px;
  background-color: $herd;
  display: flex;
  gap: 20px;
  text-decoration: none;
  cursor: pointer;
  flex-shrink: 0;

  p {
    font-size: 12px;
    font-weight: 600;
  }
}
.ai-form-container {
  width: 100%;
  min-height: calc(100vh - 200px);
  padding: 100px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

.ai-form-container-headerful {
  min-height: calc(100vh - 280px);
}

.ai-block-title {
  font-size: 300%;
  font-weight: 700;
  margin-bottom: 50px;
  width: 400px;
}

.ai-block-sub-title {
  width: 400px;
  font-size: 175%;
  font-weight: 500;
  color: $herd;
}

.ai-form-vet .ai-block-sub-title {
  color: $vet;
}
.dist-module .ai-block-sub-title {
  color: $dist;
}
.feed-module .ai-block-sub-title {
  color: $feed;
}

.ai-success-block {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ai-success-block-show {
  display: flex;
}

.ai-success-block ion-icon {
  color: $mainBlack;
  font-size: 750%;
}

.herd-module {
  .ai-success-block ion-icon {
    color: $herd;
  }
}
.vet-module {
  .ai-success-block ion-icon {
    color: $vet;
  }
}
.dist-module {
  .ai-success-block ion-icon {
    color: $dist;
  }
}
.feed-module {
  .ai-success-block ion-icon {
    color: $feed;
  }
}

/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ALL INPUT ELEMENTS */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
/* ///////////////////////// */
.ai-double-input-block {
  width: calc(420px - 0px);
  height: 60px;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 22px 0 42px 0;
  position: relative;
}

.ai-input-block-half {
  height: 100%;
  width: calc(50% - 44px);
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
  padding: 0 20px;
}

.ai-triple-input-block {
  width: calc(420px - 0px);
  height: 60px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px;
  margin: 22px 0 42px 0;
  position: relative;
}
.ai-detail-info-block {
  width: calc(420px - 40px);
  padding: 0 20px;
  height: 60px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 22px 0 42px 0;
  position: relative;
  background-color: #f0f0f0;
}
.ai-di-indicator {
  width: 20px;
  height: 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ai-di-indicator-inner {
  background-color: $yellow;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  position: absolute;
}
.ai-di-indicator-inner-1 {
  animation: 3s infinite 3s indi;
}
.ai-di-indicator-inner-2 {
  animation: 3s infinite 1.5s indi;
}
.ai-di-indicator-inner-3 {
  animation: 3s infinite indi;
}
@keyframes indi {
  from {
    width: 5px;
    height: 5px;
    opacity: 1;
  }
  to {
    width: 20px;
    height: 20px;
    opacity: 0;
  }
}
.ai-di-text {
  font-size: 16px;
  font-weight: 600;
  flex-grow: 1;
}
.ai-di-date {
  font-size: 16px;
  font-weight: 600;
}
.ai-input-block {
  width: calc(420px - 40px);
  padding: 0 20px;
  height: 60px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 22px 0 42px 0;
  position: relative;
}

.ai-combined-block .ai-input-block {
  margin: 30px 0 0px 0;
}

.ai-input-block-text {
  background-color: #f0f0f0;
  gap: 20px;
}

.ai-input-block-textarea {
  height: 240px;
}

.ai-input-block-select {
  background-color: #f0f0f0;
  justify-content: center;
}

.ai-input-block-pick {
  width: calc(420px - 0px);
  padding: unset;
}
.ai-input-block-pick-big {
  height: unset;
  display: block;
}

.ai-input-block-small-select {
  width: calc(420px - 0px);
  padding: 0;
}

.ai-problem-block {
  width: calc(420px - 40px);
  padding: 0 20px;
  min-height: 60px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin: 22px 0 42px 0;
  position: relative;
  background-color: #f0f0f0;
  /* overflow: hidden; */
  transition: 0.2s;
  cursor: pointer;
}
.ai-input-center {
  text-align: center;
}

.ai-problem-header {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  z-index: 100;
  gap: 20px;
}
.ai-problem-icon {
  height: 45px;
  width: 45px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: 60%;
    height: auto;
  }
}
.ai-problem-text {
  font-size: 16px;
  font-weight: 600;
}

.ai-problem-sub-text {
  color: #545454;
}

.ai-problem-detail {
  /* color: #545454; */
  font-size: 16px;
  font-weight: 600;
  width: calc(100% - 65px);
  padding: 0 0 20px 65px;
  background-color: #f0f0f0;
  border-radius: 10px;
  display: none;
}

.ai-selected-animals-block {
  width: 420px;
  position: relative;
  margin: 22px 0 42px 0;
  display: none;
}

.ai-selected-animals-item {
  font-size: 16px;
  font-weight: 600;
  padding: 12.5px 17.5px;
  border-radius: 50px;
  background-color: #f0f0f0;
  float: left;
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 10px;

  &:hover .ai-selected-animals-remove {
    opacity: 1;
  }
}

.ai-selected-animals-remove {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50px;
  background-color: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
}

.ai-select-line {
  position: absolute;
  height: 1.5px;
  width: 10%;
  bottom: 6px;
  background-color: $mainBlack;
}

.ai-input-label {
  font-size: 15px;
  font-weight: 600;
  color: #545454;
  position: absolute;
  top: -3px;
  left: 0;
  transform: translateY(-100%);
}

.ai-combined-block .ai-input-label {
  color: $mainBlack;
}

.ai-input {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 600;
  flex-grow: 1;
  height: 100%;
  box-sizing: border-box;
  min-width: 10px;
}

.ai-input-select {
  text-align: center;
}

.ai-input-small-select {
  background-color: #f0f0f0;
  padding: 0 20px;
  border-radius: 10px;
}

.ai-textarea {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 600;
  flex-grow: 1;
  height: calc(100% - 0px);
  padding: 15px 0;
  box-sizing: border-box;
  resize: none;
}

/* Hide arrow for input number */
.ai-input::-webkit-outer-spin-button,
.ai-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.ai-input {
  -moz-appearance: textfield;
}

.ai-inside-text {
  font-size: 15px;
  font-weight: 600;
}

.ai-warning-text {
  font-size: 14px;
  font-weight: 600;
  color: $error;
  bottom: -3px;
  left: 0;
  transform: translateY(100%);
  position: absolute;
}
.ai-big-warning-text {
  color: $error;
  font-size: 14px;
  font-weight: 600;
  margin-top: 20px;
}

/* Big select */
.ai-select-block {
  position: absolute;
  width: calc(100% - 0px);
  max-height: 300px;
  border-radius: 10px;
  background-color: #f0f0f0;
  overflow: auto;
  bottom: -8px;
  transform: translateY(100%);
  z-index: 100;
  display: none;
  left: 0;
  overflow: hidden;

  &:hover {
    overflow-y: scroll;
  }
}

/* .ai-select-block::-webkit-scrollbar {
  display: none;
} */
.ai-select-block::-webkit-scrollbar-track {
  width: 5px;
  background-color: #00000000;
}

.ai-select-item {
  width: calc(100% - 40px);
  padding: 0 20px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  &:hover {
    background-color: $herd;
  }
}

.ai-select-item-selected {
  background-color: $herd;
}

.ai-select-item-unvail {
  opacity: 0.5;
  pointer-events: none;
}

.ai-select-name {
  font-size: 16px;
  font-weight: 600;
}

.ai-select-sub-name {
  font-size: 16px;
  font-weight: 600;
  opacity: 0.75;
}
.ai-select-mark {
  display: flex;
  align-items: center;
  justify-content: center ;
  background-color: #d9d9d9;
  border-radius: 5px;
  width: 25px;
  height: 25px;
  font-size: 14px;
  font-weight: 600;
}
/* Small select */
.ai-small-select {
  background-color: #f0f0f0;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  height: 100%;
  min-width: 30px;
  padding: 0 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.ai-small-select-full {
  flex-grow: 1;
}

.ai-small-select p {
  font-size: 16px;
  font-weight: 600;
}

.ai-small-select-block {
  position: absolute;
  left: 0;
  bottom: -10px;
  transform: translateY(100%);
  background-color: #f0f0f0;
  border-radius: 10px;
  z-index: 100;
  min-width: 100%;
  display: none;
}

.ai-small-select-item {
  height: 60px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;

  &:hover {
    background-color: $herd;
  }

  &:first-of-type {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  &:last-of-type {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}

.ai-small-select-item-selected {
  background-color: $herd;
}

.ai-pick {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: #f0f0f0;
  flex-grow: 1;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;

  ion-icon {
    font-size: 30px;
  }
}
.ai-pick-active#herd {
  background-color: $herd;
}
.ai-pick-active#vet {
  background-color: $vet;
}
.ai-pick-active#dist {
  background-color: $dist;
}
.ai-pick-active#feed {
  background-color: $feed;
}
.ai-pick-color-g.ai-pick-active {
  background-color: $green;
}
.ai-pick-color-y.ai-pick-active {
  background-color: $yellow;
}
.ai-pick-color-r.ai-pick-active {
  background-color: $red;
}

.ai-pick-active {
  background-color: $mainBlack;
  color: #f0f0f0;

  ion-icon {
    color: #f0f0f0;
  }
}

.ai-pick-unav {
  pointer-events: none;
  opacity: 0.5;
}
.ai-input-block-pick-big .ai-pick {
  height: 60px;
  padding: 0 15px;
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
}
.ai-input-block-pick-small .ai-pick {
  height: 40px;
  padding: 0 15px;
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  font-size: 14px;
}

.ai-input-submit-btn {
  height: 60px;
  width: 170px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  background-color: $herd;
  color: $mainBlack;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;

  border: none;
  outline: none;
  box-shadow: unset;

  /* pointer-events: none;
  filter: grayscale(1); */

  &:hover {
    opacity: 0.9;
  }
}

.ai-form-vet .ai-input-submit-btn {
  background-color: $vet;
}
.dist-module .ai-input-submit-btn {
  background-color: $dist;
}
.feed-module .ai-input-submit-btn {
  background-color: $feed;
}

.ai-input-marker {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: -35px;
  background-color: #f0f0f0;

  top: calc(50% - 12.5px);
}

.ai-input-marker-s {
  background-color: $success;
}

.ai-input-marker-f {
  background-color: $error;
}

.ai-input-marker ion-icon {
  opacity: 0.5;
}

.ai-input-marker-s ion-icon,
.ai-input-marker-f ion-icon {
  color: #ffffff;
  opacity: 1;
}

.ai-input-explain-block {
  padding: 15px;
  background-color: #f0f0f0;
  border-radius: 10px;
  position: absolute;
  max-width: calc(200px - 30px);
  min-height: calc(60px - 30px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
}

.ai-input-explain-block-explain {
  top: 0;
  right: -55px;
  transform: translate(100%);
}

.ai-input-explain-block-required {
  top: calc(50% - 30px);
  left: -55px;
  transform: translate(-100%);
}

.ai-input-explain-block-info {
  top: 0;
  right: -20px;
  transform: translate(100%);
  opacity: 1;
}

.ai-input-eb-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

.ai-input-eb-text {
  font-size: 14px;
  font-weight: 600;
